import './App.css';

import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link
} from "react-router-dom";

import {CoronaSearchBar} from "./components/SearchBar.tsx"
import {RandomOWIDGraph} from "./components/RandomOWIDGraph.tsx"


function App() {

  return (
    <div className="App">
      <Router>
        <Link to="/covid-search">OWID Covid Data Explorer</Link>
        <br/>
        <Link to="/random-graph">Random OWID Graph</Link>
        <Switch>
          <Route path="/covid-search">
            <CoronaSearchBar/>
          </Route>
          <Route path="/random-graph">
            <RandomOWIDGraph/>
          </Route>
        </Switch>
      </Router>
    </div>
  );
}

export default App;
